
import XButton from 'vux/src/components/x-button/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import player from '../../common/Player';
export default{
name: 'articleIndex',
directives: {
TransferDom
},
components:{
XButton,
LoadMore,
Countup,
player,
InlineLoading,
Scroller,
FlexboxItem,
Flexbox,
XImg,
Previewer,
ViewBox,
XHeader,
XTextarea,
XInput,
Group,
Badge,
Alert,
Toast,
Sticky,
Tab,
TabItem
},
data(){
return {
stickyDisabled:typeof navigator !== 'undefined' && /iphone/i.test(navigator.userAgent) && /ucbrowser/i.test(navigator.userAgent),
pullupEnabled: true,
isFollowSort:false,
isLogin:false,
articleSort:{},
queryObj:{
sort_article_id:-1,
sort_article_name:'',
essence:'0',
currentPage:1,
pageSize:10,
count:0,
pageCount:0,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
list:[]
},
getBarWidth:(index)=>{
return (1 + 1) * 22 + 'px'
}
}
},
created(){
this.isLogin = this.$store.getters.isLogin
this.currentUser = this.$store.getters.currentUser
this.queryObj.sort_article_id = this.$route.query.sort_article_id
this.$Apis.insertLatelys(this.queryObj.sort_article_id)
this.initFunc()
},
methods:{
loadMore () {
this.queryObj.currentPage++
this.queryEvt()
},
refresh () {
setTimeout(() => {
this.queryObj.currentPage = 1
this.queryObj.list = []
this.queryEvt()
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
}, 2000)
},
initFunc(){
this.queryById()
this.queryEvt()
},
userIndexEvt(user_id){
let isCurrentUser = false;
if(this.$store.getters.currentUser){
if(this.$store.getters.currentUser.user_id == user_id){
isCurrentUser = true
}
}
this.$router.push({
name:'userIndexLink',
query:{
isCurrentUser:isCurrentUser,
user_id:user_id
}
})
},
isFollowSortEvt(){
let params = {
user_id : this.currentUser.user_id,
article_sort_id:this.queryObj.sort_article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORTUSER_ISFOLLOW,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
this.isFollowSort = res.data.length>0?true:false
}else{
this.$vux.toast.text('查看是否关注过吧错误', 'bottom')
}
}).catch(err=>console.log("查看是否关注过吧错误："+err))
},
addFollowSortEvt(){
if(!this.isLogin){
this.$router.push({
name:'loginLink',
query:{
article_id:this.queryObj.sort_article_id
}
})
}
let params = {
user_id : this.currentUser.user_id,
article_sort_id:this.queryObj.sort_article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORTUSER_INSERT,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
this.$vux.toast.text('关注成功', 'bottom')
this.isFollowSortEvt()
}else{
this.$vux.toast.text('关注失败', 'bottom')
}
}).catch(err=>console.log("查看是否关注过吧错误："+err))
},
queryById(){
let params = {
sort_article_id:this.queryObj.sort_article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_QUERYBYID,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
this.articleSort = res.data[0]
}else{
this.$vux.toast.text('系统修改查看人数错误', 'bottom')
}
}).catch(err=>console.log("系统修改查看人数错误："+err))
},
queryEvt(){
let params = {
sort_article_id:this.queryObj.sort_article_id,
essence:this.queryObj.essence,
currentPage:this.queryObj.currentPage,
pageSize:this.queryObj.pageSize,
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_INDEX,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.list.length>0){
res.data.list.forEach(item=>{
item.article_time = this.$Apis.getDateDiff(new Date(item.article_time).getTime())
this.queryObj.list.push(item)
})
this.queryObj.currentPage = res.data.currentPage
this.queryObj.pageSize = res.data.pageSize
this.queryObj.count = res.data.count
this.queryObj.pageCount = res.data.pageCount
if(this.isLogin){
this.isFollowSortEvt()
}
this.queryObj.status.pullupStatus = 'default'
this.$refs.scroller.reset()
if(this.queryObj.currentPage>=this.queryObj.pageCount){
this.queryObj.status.pullupStatus = 'disabled' 
}
}else{
this.queryObj.status.pullupStatus = 'disabled' 
}
}else{
this.$vux.toast.text('查看帖子失败', 'bottom')
}
}).catch(err=>console.log("查看帖子失败错误："+err))
},
detailEvt(id){
this.$router.push({
name:'articleIndexLink',
query:{
article_id:id
}
})
},
essenceEvt(essence){
this.queryObj.essence = essence
this.queryObj.list = []
this.queryObj.status= {
pullupStatus: 'default',
pulldownStatus: 'default'
}
this.queryObj.currentPage = 1
this.queryObj.pageSize = 10
this.queryObj.count = 0
this.queryObj.pageCount = 0
this.queryEvt()
},

insertEvt(){
this.$router.push({
path:'/article/insert',
query:{
type_id:0,
sort_article_id:this.queryObj.sort_article_id,
sort_article_name:this.articleSort.sort_article_name
}
})
}
}
}



