
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Countup from 'vux/src/components/countup/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import Spinner from 'vux/src/components/spinner/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import player from '../../common/Player';
export default {
directives: {
TransferDom
},
components: {
player,
InlineLoading,
LoadMore,
XImg,
Countup,
Spinner,
Scroller,
Flexbox,
FlexboxItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
Panel,
SwiperItem
},
data () {
return {
indexObj:{
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
currentPage:1,
pageSize:20,
count:0,
pageCount:0,
list:[]
},
followIndexObj:{
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
user_id:'',
currentPage:1,
pageSize:20,
count:0,
pageCount:0,
list:[]
},
videoObj:{
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
currentPage:1,
pageSize:20,
count:0,
pageCount:0,
list:[]
},
pageData:{
headerNav: ['关注','首页','视频'],
headerIndex: 1,
selected:{},
swiperHeight:'0'
},

}
},
created() {
this.$Apis.inserttabbarSelected(1)
this.pageData.swiperHeight = (document.documentElement.clientHeight-46-50)+"px"
this.indexEvt()
if(this.$store.getters.isLogin){
this.followIndexObj.user_id = this.$store.getters.currentUser.user_id
this.followIndexEvt()
}
},
methods:{
searchEvt(){
this.$router.push({
name:'searchsLink'
})
},
indexLoadMore () {
this.indexObj.currentPage++
this.indexEvt()
},
indexRefresh () {
setTimeout(() => {
this.indexObj.currentPage = 1
this.indexObj.list = []
this.indexEvt()
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.$refs.scroller.enablePullup()
}, 10)
})
}, 2000)
},
indexEvt(){
let params = {
currentPage:this.indexObj.currentPage,
pageSize:this.indexObj.pageSize
}
this.$Axios.post(this.$Urls.POST_ARTICLE_INDEX,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.list.length>0){
res.data.list.forEach(el=>{
el.article_time = this.$Apis.getDateDiff(new Date(el.article_time).getTime())
this.indexObj.list.push(el)
})
this.indexObj.list = res.data.list
this.indexObj.currentPage = res.data.currentPage
this.indexObj.pageSize = res.data.pageSize
this.indexObj.count = res.data.count
this.indexObj.pageCount = res.data.pageCount

this.indexObj.status.pullupStatus = 'default'
this.indexObj.status.pulldownStatus = 'default'
this.$refs.scroller.reset()
if(this.indexObj.currentPage >= this.indexObj.pageCount){
this.indexObj.status.pullupStatus = 'disabled' 
}
}else{
this.indexObj.status.pulldownStatus = 'default'
this.indexObj.status.pullupStatus = 'disabled' 
}
}else{
this.indexObj.status.pullupStatus = 'default'
this.indexObj.status.pulldownStatus = 'default'
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.indexObj.status.pullupStatus = 'default'
this.indexObj.status.pulldownStatus = 'default'
this.$vux.toast.text('系统错误', 'bottom')
console.log("系统错误："+err)

})
},
followIndexLoadMore () {
this.followIndexObj.currentPage++
this.followIndexEvt()
},
followIndexRefresh () {
setTimeout(() => {
this.followIndexObj.currentPage = 1
this.followIndexObj.list = []
this.followIndexEvt()
this.$nextTick(() => {
setTimeout(() => {
this.$refs.followIndexScroller.donePulldown()
this.$refs.followIndexScroller.enablePullup()
}, 10)
})
}, 2000)
},
followIndexEvt(){
if(!this.$store.getters.isLogin){
return false
}
let params = {
user_id:this.followIndexObj.user_id,
currentPage:this.followIndexObj.currentPage,
pageSize:this.followIndexObj.pageSize
}
this.$Axios.post(this.$Urls.POST_ARTICLE_FOLLOWINDEX,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.list.length>0){
res.data.list.forEach(el=>{
el.article_time = this.$Apis.getDateDiff(new Date(el.article_time).getTime())
this.followIndexObj.list.push(el)
})
this.followIndexObj.currentPage = res.data.currentPage
this.followIndexObj.pageSize = res.data.pageSize
this.followIndexObj.count = res.data.count
this.followIndexObj.pageCount = res.data.pageCount

this.followIndexObj.status.pullupStatus = 'default'
this.followIndexObj.status.pulldownStatus = 'default'
this.$refs.followIndexScroller.reset()
if(this.followIndexObj.currentPage >= this.followIndexObj.pageCount){
this.followIndexObj.status.pullupStatus = 'disabled' 
}
}else{
this.followIndexObj.status.pulldownStatus = 'default'
this.followIndexObj.status.pullupStatus = 'disabled' 
}
}else{
this.followIndexObj.status.pullupStatus = 'default'
this.followIndexObj.status.pulldownStatus = 'default'
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.followIndexObj.status.pullupStatus = 'default'
this.followIndexObj.status.pulldownStatus = 'default'
this.$vux.toast.text('系统错误', 'bottom')
console.log("系统错误："+err)

})
},
detailEvt(id){
this.$router.push({
name:'articleIndexLink',
query:{
article_id:id
}
})
},
sortArticleEvt(sort_article_id){
this.$router.push({
name: 'articleSortIndexLink',
query: {
sort_article_id: sort_article_id
}
})
}

}


}



