export default{
directives: {
TransferDom
},
components: {
InlineLoading,
LoadMore,
XImg,
Countup,
Spinner,
Scroller,
Flexbox,
FlexboxItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
Panel,
SwiperItem
},
data(){
return{
